@import url(https://fonts.googleapis.com/css?family=Roboto:300,400,900);
* {
    box-sizing: border-box;
    margin: 0;
    padding: 0;
}

body {
    font-family: "Roboto", -apple-system, BlinkMacSystemFont, "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
    font-weight: 300;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    display: -webkit-flex;
    display: flex;
    min-height: 100vh;
    -webkit-flex-direction: column;
            flex-direction: column;
    margin: 0;
    font-size: 16px;
}

#root {
    display: -webkit-flex;
    display: flex;
    -webkit-flex: 1 1;
            flex: 1 1;
}

.roboto-light {
    font-family: "Roboto", sans-serif;
    font-weight: 300;
}

.roboto-regular {
    font-family: "Roboto", sans-serif;
    font-weight: 400;
}

.roboto-black {
    font-family: "Roboto", sans-serif;
    font-weight: 900;
}

.color-red {
    color: #D42026;
}

.background-red {
    background-color: #D42026;
}

.color-black {
    color: #4e4e4e;
}

.color-white {
    color: #ffffff;
}

.search__input {
    display: block;
    background-clip: padding-box;
    border-radius: .25rem;
    overflow: visible;
}

.search__input:focus {
    outline: none;
}

.table {
    border-collapse: collapse;
    width: 100%;
}

.table tr {
    display: table-row;
    border-color: inherit;
}

.table th {
    border-right: 10px solid #fff;
}

.table th:last-child {
    border-right: none;
}

.table th {
    text-align: left;
    width: 25%;
}

.table td:last-child, th:last-child {
    width: 50%;
}

.table tbody tr:hover {
    cursor: pointer;
    background-color: #fbe9e9;
}

.paginator {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: row;
            flex-direction: row;
    -webkit-flex: 1 1;
            flex: 1 1;
    -webkit-align-items: center;
            align-items: center;
    -webkit-justify-content: center;
            justify-content: center;
}

.page-number {
    width: 32px;
    height: 32px;
    border: 1px solid #d42026;
    border-radius: 300px;
    font-size: 21px;
    line-height: 1.1;
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-items: center;
            align-items: center;
    margin-right: 20px;
    cursor: pointer;
}

.paginator div:nth-last-child(2) {
    margin-right: 0;
}

.search-page {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-flex: 1 1;
            flex: 1 1;
}

